import React, {Component} from 'react';
import './projects.css'

/* Importing library for DOM elements animations */
import Zoom from 'react-reveal/Zoom';
import Pulse from 'react-reveal/Pulse';


/* Imaporting card flip component */
import Card from '../card';


export default class projects extends Component {

    constructor() {
      super();

      this.state = {
      };

      this.handleClick = this.handleClick.bind(this);

      setTimeout(
        function() {
          this.setState({isFlipped: true});
        }
        .bind(this),
        1625
      );

    }

    handleClick(e) {
      e.preventDefault();
      this.setState(prevState => ({ isFlipped: !prevState.isFlipped }));
    }

    render() {

      return (
        <div className="component-projects">
          <div className="container">

            <div className="row">

              <header className="col-12 projects-header">
                <Pulse duration={750}>
                  <h1 className="font-weight-600"> Here you can view my projects! </h1>
                </Pulse>

                <Pulse duration={750}>
                  <h4> Hover or click photos to get additional information! </h4>
                </Pulse>
              </header>

            </div>

            <main className="cards-containter">

            <Zoom>
              <div className="row projects-icons-wrapper">

                <Card Title={"Never-Forget"} FrontImageSource={"neverforget.png"} BackText="Application that allows you to organize research!" Link = "https://github.com/Dolidodzik/foodie" />
                <div className="card-spacer"></div>
                <Card Title={"Typing"} FrontImageSource={"typing.png"} BackText="In this app you can check your typing speed!" Link = "https://github.com/Dolidodzik/typingspeedtest" />
                <div className="card-spacer"></div>
                <Card Title={"Portfolio"} FrontImageSource={"portfolio.png"} BackText="My portfolio that you are looking on right now!" Link = "https://github.com/Dolidodzik/portfolio" />
                <div className="card-spacer"></div>
                <Card Title={"Foodie"} FrontImageSource={"foodie.png"} BackText="Hungry? Here you can find nearby restaurants!" Link = "https://github.com/Dolidodzik/foodie" />
                <div className="card-spacer"></div>
                <Card Title={"Gallery"} FrontImageSource={"gallery.png"} BackText="Simple, but functional image gallery!" Link = "https://github.com/Dolidodzik/django_wagtail_image_gallery" />
                <div className="card-spacer"></div>
                <Card Title={"Blog"} FrontImageSource={"blog.png"} BackText="Wagtail blog, with customizable post, pages, and other utilities!" Link = "https://github.com/Dolidodzik/Wagtail-Django-Static-Blog" />
                <div className="card-spacer"></div>




              </div>
            </Zoom>

            </main>

          </div>
        </div>
      );
    }
  }
